.connect {
	height: 60vh;
	width: 95%;
	margin-top: 50px;
	margin-left: 50px;
	margin-bottom: 50px;
	padding-top: 0px;
	overflow: hidden;
}

.headingC {
	font-size: 60px;
	margin-left: 30px;
	font-weight: 500;
}
.col-md-7 {
	padding-left: 50px;
}

@media only screen and (max-width: 600px) {
	.connect {
		height: 60vh;
		width: 90vw;
		margin-top: 50px;
		margin-left: 30px;
		border: 1px;
		border-radius: 40px;
		box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
			-5px -5px 30px 7px rgba(0, 0, 0, 0.22);
	}
	.headingC {
		font-size: 40px;
		font-weight: 500;
		margin-left: 30px;
	}
	.icon-item {
		height: 35px;
	}
}
